import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { localStoragedecryp } from "../../../../../components/helpers/function";
class ExportPenjualanSalesExel extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <>
        <ReactHTMLTableToExcel
           id="test-table-xls-button"
           className="btn btn-success btn-block"
           table="table-to-xls"
           filename="Laporan Transaksi Penjualan Sales"
           sheet="Laporan Transaksi Penjualan Sales"
           buttonText="Export Exel"
         />
        <table id="table-to-xls" style={{ display: "none" }}>
          <thead>
            <tr>
              <th colSpan="12" style={{ textAlign: "center" }}>
                {" "}
                LAPORAN TRANSAKSI PENJUALAN SALES{" "}
              </th>
            </tr>
                <tr>
                      <th colSpan="12"> Periode  </th>
                </tr>
                <tr>
                      <th colSpan="12"> {localStoragedecryp('tgl_laporan').length === 0 ? null : JSON.parse(localStoragedecryp('tgl_laporan')).tgl_awal + ' s/d ' + JSON.parse(localStoragedecryp('tgl_laporan')).tgl_akhir} </th>
                </tr>
                <tr>
                  <th colSpan="12"></th>
            </tr>
          </thead>
        {this.props.data.map((item, index) => (
                <>
            <tbody>
                <tr>
                    <td style={{textAlign: "left"}}>Kode Sales : {item._id.kode_sales}</td>
                    <td colSpan="11"> </td>
                </tr>
                <tr>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>NO FAKTUR</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>KODE DEPT</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>KODE BARCODE</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>NAMA BARANG</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>BERAT</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>ONGKOS</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>HARGA JUAL</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>NAMA ATRIBUT</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>BERAT ATRIBUT</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>HARGA ATRIBUT</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>HARGA GRAM</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>HARGA TOTAL</td>
                </tr>
            {item.detail.map((row, index) => (
              <tr>
                <td>{row.no_faktur_jual}</td>
                <td>{row.kode_dept}</td>
                <td>{row.kode_barcode}</td>
                <td style={{ whiteSpace: "nowrap" }}>
                  {row.nama_barang}
                </td>
                <td style={{ textAlign: "center" }}>{row.berat.toFixed(3)}</td>
                <td style={{ textAlign: "center" }}>{row.ongkos}</td>
                <td style={{ textAlign: "center" }}>{row.harga_jual}</td>
                <td>{row.nama_atribut}</td>
                <td style={{ textAlign: "center" }}>{row.berat_atribut.toFixed(3)}</td>
                <td style={{ textAlign: "center" }}>{row.harga_atribut}</td>
                <td style={{ textAlign: "center" }}>{row.harga_gram}</td>
                <td style={{ textAlign: "center" }}>{row.harga_total}</td>
              </tr>
            ))}
          </tbody>
          <tfoot>
          <tr>
            <td colSpan="4" style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "center" }}>
              Total Barang :  {item.detail.length} 
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "center" }}> {item.detail.map(list => list.berat).reduce((a,b) => a+b,0)}  </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "center" }}> {item.detail.map(list => list.ongkos).reduce((a,b) => a+b,0)}  </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "center" }}> {item.detail.map(list => list.harga_jual).reduce((a,b) => a+b,0)} </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "center" }}> </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "center" }}> {item.detail.map(list => list.berat_atribut).reduce((a,b) => a+b,0)}  </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "center" }}> {item.detail.map(list => list.harga_atribut).reduce((a,b) => a+b,0)} </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "center" }}> {item.detail.map(list => list.harga_gram).reduce((a,b) => a+b,0)} </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "center" }}> {item.detail.map(list => list.harga_total).reduce((a,b) => a+b,0)} </td>
            </tr>
            <tr>
                <td colSpan="12"></td>
            </tr>
            </tfoot>
          </>
          
        ))}
              
        </table>
      </>
    );
  }
}
export default ExportPenjualanSalesExel;
