import jsPDF from "jspdf";
import "jspdf-autotable";
import { getToday, localStoragedecryp } from "../../../../../components/helpers/function";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const ExportLaporanPenjualanSales = (
  data = "",
  username = "",
  tanggal_dari = "",
  tanggal_sampai = "",
) => {
  // initialize jsPDF
  const doc = new jsPDF("l", "mm", [297, 210]);
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let tableColumn = [];
  let finalY = 30;
  let total_baraang = 0;
  let total_berat = 0;
  let total_onkos = 0;
  let total_harga_jual = 0;
  let total_berat_atribut = 0;
  let total_harga_atribut = 0;
  let total_harga_gram = 0;
  let total_harga_total = 0;

  doc.setFontSize(15);
  doc.text("LAPORAN PENJUALAN SALES", 14, 15);
  doc.setFontSize(10);
  doc.setProperties({
    title: "LAPORAN PENJUALAN SALES"
  });
  //row 1
  doc.text(
    `PERIODE : ${
      localStoragedecryp("tgl_laporan").length === 0
        ? null
        : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
          " s/d " +
          JSON.parse(localStoragedecryp("tgl_laporan")).tgl_akhir
    }`,
    14,
    25
  );
  
  data.forEach((item, index) => {
    tableColumn = [
      [
        {
          content: `KODE SALES : ${item._id.kode_sales}`,
          colSpan: 12,
        },
      ],
      [
        {
          content: `NO FAKTUR`,
        },
        {
          content: `KODE DEPT`,
        },
        {
          content: `KODE BARCODE`,
        },
        {
          content: `NAMA BARANG`,
        },
        {
          content: `BERAT`,
        },
        {
          content: `ONGKOS`,
        },
        {
          content: `HARGA JUAL`,
        },
        {
          content: `NAMA ATRIBUT`,
        },
        {
          content: `BERAT ATRIBUT`,
        },
        {
          content: `HARGA ATRIBUT`,
        },
        {
          content: `HARGA GRAM`,
        },
        {
          content: `HARGA TOTAL`,
        },
      ],
    ];
    let sub_total = 0;
    let sub_harga_gram = 0;
    let sub_harga_atribut = 0;
    let sub_berat_atribut = 0;
    let sub_harga_jual = 0;
    let sub_ongkos = 0;
    let sub_berat = 0;
    item.detail.forEach((item, index) => {
      let rows = [
        item.no_faktur_jual,
        item.kode_dept,
        item.kode_barcode,
        item.nama_barang,
        (item.berat).toFixed(3),
        parseInt(item.ongkos).toLocaleString("ID-id"),
        parseInt(item.harga_jual).toLocaleString("ID-id"),
        item.nama_atribut,
        (item.berat_atribut).toFixed(3),
        parseInt(item.harga_atribut).toLocaleString("ID-id"),
        parseInt(item.harga_gram).toLocaleString("ID-id"),
        parseInt(item.harga_total).toLocaleString("ID-id"),
      ];

      total_berat = total_berat + parseFloat(item.berat);
      total_onkos = total_onkos + parseInt(item.ongkos);
      total_harga_jual = total_harga_jual + parseInt(item.harga_jual);
      total_berat_atribut = total_berat_atribut + parseInt(item.berat_atribut);
      total_harga_atribut = total_harga_atribut + parseInt(item.harga_atribut);
      total_harga_gram = total_harga_gram + parseInt(item.harga_gram);
      total_harga_total = total_harga_total + parseInt(item.harga_total);

      sub_harga_jual = sub_harga_jual+ parseFloat(item.harga_jual);
      sub_harga_atribut = sub_harga_atribut+ parseFloat(item.harga_atribut);
      sub_berat_atribut = sub_berat_atribut + parseFloat(item.berat_atribut);
      sub_harga_gram = sub_harga_gram + parseFloat(item.harga_gram);
      sub_total = sub_total + parseFloat(item.harga_total);
      sub_ongkos = sub_ongkos + parseFloat(item.ongkos);
      sub_berat = sub_berat + parseFloat(item.berat);
      tableRows.push(rows);
    })

    total_baraang = total_baraang+item.detail.length;
    // localStorage.setItem('hasil',JSON.stringify(item.detail.length))
    // console.log();
    let total = [
      {
        content: `Total Barang : ${item.detail.length}`,
        colSpan: 4,
        styles: {
          halign: 'center',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content: `${sub_berat.toFixed(3)}`,
        styles: {
          halign: 'center',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content: `${parseInt(sub_ongkos).toLocaleString("ID-id")}`,
        styles: {
          halign: 'center',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content: `${parseInt(sub_harga_jual).toLocaleString("ID-id")}`,
        styles: {
          halign: 'center',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content: ``,
        styles: {
          halign: 'center',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content: `${sub_berat_atribut.toFixed(3)}`,
        styles: {
          halign: 'center',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content: `${parseInt(sub_harga_atribut).toLocaleString("ID-id")}`,
        styles: {
          halign: 'center',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content: `${parseInt(sub_harga_gram).toLocaleString("ID-id")}`,
        styles: {
          halign: 'center',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content: `${parseInt(sub_total).toLocaleString("ID-id")}`,
        styles: {
          halign: 'center',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
    ];
    tableRows.push(total);
  });
 
  let printed = [
    {
      content: `Printed By ${JSON.parse(localStoragedecryp("userdata")).user_id} / ${getToday()}`,
      colSpan: 12,
      styles: {
        // lineWidth: 0.1,
        fontStyle: "italic",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
  ]
  tableRows.push(printed);
    
    doc.autoTable({
      head: tableColumn,
      body: tableRows,
      // foot: footRows,
      startY: finalY,
      theme: "plain",
      rowPageBreak: "avoid",
      // pageBreak: "avoid",
      margin: { top: 10 },
      bodyStyles: {
        // lineWidth: 0.02, lineColor: "#000",
        fontSize:8,
      },
      headStyles: {
        fontSize:8,
        // lineWidth: 0.02,
        // lineColor: "#000",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
      tableLineColor: [255, 255, 255],
      tableLineWidth: 1,
    });
    finalY = doc.autoTableEndPosY() + 3;
    tableRows = [];
    // footRows = [];
    // sub_total = 0;
    // sub_harga_gram = 0;
    // sub_harga_atribut = 0;
    // sub_berat_atribut = 0;
    // sub_harga_jual = 0;
    // sub_ongkos = 0;
    // sub_berat = 0;
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left

  // doc.text(`User                        : ${JSON.parse(localStoragedecryp("userdata")).user_id}`, 14, finalY + 10);
  // doc.text(`Cetak                      : ${getToday()}`, 14, finalY + 16);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  // doc.autoPrint();
  // doc.save(`LAPORAN PENJUALAN SALES.pdf`);
  var string = doc.output("datauristring");
  // var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
  var x = window.open();
  x.document.open();
  x.document.write("<html><head><title>LAPORAN PENJUALAN SALES</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" + 
  string + "></embed></body></html>");
  // x.document.write(embed);
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default ExportLaporanPenjualanSales;

